.navbar{
    background:rgb(1,1,1);
    height:5rem;
    width: 100vw;
    margin: 0 auto;
    max-width: 75rem;
    padding: 1rem 2rem;
    display:flex;
    align-items:center;
    justify-content:space-between ; /*Doubt*/
    position:sticky;
    top:0;
    z-index:3;
}

.logo{
    object-fit:cover;
    height: 3rem;
    width: 4rem;
    border-radius: 2rem;
    background-color: black;

}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}
.desktopMenuListItem:hover{
    color:yellow;
    padding-bottom: 0.5rem;
    border-bottom:3px solid yellow;
}
.desktopMenuBtn
{
    background:white;
    color:black;
    border:none;
    display: flex;
    align-items: center;
    justify-content:center;
    padding:0 1rem;
    border-radius: 2rem;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    height: 1rem;
}
.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 3rem;
}
.navMenu{
    position:absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}
.listItem{
    color: white;
    padding: 0.5rem;
    margin: 0.25rem;
    background:rgb(25,25,25);
}
@media screen and (max-width:720px) {
    
    .mobMenu{
        display: flex;

    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}